.App {
  text-align: center;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.content{
    padding: 20px;
    display: -ms-flexbox;
    display: flex;
}
.left{
  -ms-flex:1 1;
      flex:1 1;
  min-height: 400px;
  border-right: 1px solid #ccc;
}
.right{
  -ms-flex:4 1;
      flex:4 1;
  min-height:400px;
  padding: 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.left > a{
  display: block;
  text-decoration: none;
  margin-top: 20px;
}
.left > a:hover{
  color: red;
}

@-webkit-keyframes Rotate
{
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Rotate
{
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.menu-group{
  display: inline-block;
  position: relative;
  top: 0px;
  left: 0px;
}
.tab{
  display: inline-block;
  margin: 20px;
}
.wrap-ifont{
  display: inline-block;
}
.classColor{
  border-color: #189ff0;
  color: #189ff0;
}
p{
  margin: 0px;
}
h3{
  margin: 0px;
}
ul,li{
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.example{
  text-align: center;
  background: rgba(0,0,0,0.05);
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 30px 50px;
  margin: 20px 0;
}
.icon-wrap{
  position: absolute;
  top: 12px;
  left: 150px;
}



