.carousel-wrap{
  text-align: center;
  height: 160px;
  line-height: 160px;
  background: #364d79;
  overflow: hidden;
  position: relative;
  top: 0px;
  left: 0px;
  color: #fff;
}
.item-wrap{
  width: 1544px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.vertical-carousel{
  height: 640px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.vertical-dots{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.carousel-dots{
  position: absolute;
  bottom: 12px;
  left: center;
  text-align: center;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.dot-item{
  margin: 0 2px;
  cursor: pointer;
  background-color: #fff;
  opacity: .3;
  width: 16px;
  height: 3px;
  border-radius: 1px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
.checked{
  background: #fff;
  opacity: 1;
  width: 24px;
}



